import React, { useState, useEffect } from 'react';
import Card from './components/ui/Card'; // AsegÃºrate de que la ruta es correcta
import { X } from 'lucide-react';

const characters = [
  { id: 1, name: "Sarah", imagePath: "/images/who-is-who/1.sarah.png", features: ["blonde hair", "glasses", "beauty spot"], eliminated: false },
  { id: 2, name: "John", imagePath: "/images/who-is-who/2.john.png", features: ["brown hair", "beard", "blue eyes"], eliminated: false },
  { id: 3, name: "Maria", imagePath: "/images/who-is-who/3.maria.png", features: ["black hair", "brown eyes", "earrings"], eliminated: false },
  { id: 4, name: "James", imagePath: "/images/who-is-who/4.james.png", features: ["grey hair", "mustache", "glasses"], eliminated: false },
  { id: 5, name: "Emma", imagePath: "/images/who-is-who/5.emma.png", features: ["red hair", "freckles", "green eyes"], eliminated: false },
  { id: 6, name: "David", imagePath: "/images/who-is-who/6.david.png", features: ["bald", "beard", "brown eyes"], eliminated: false },
  { id: 7, name: "Lisa", imagePath: "/images/who-is-who/7.lisa.png", features: ["frizzy afro hair", "glasses", "earrings"], eliminated: false },
  { id: 8, name: "Michael", imagePath: "/images/who-is-who/8.michael.png", features: ["blonde hair", "blue eyes", "clean shaven"], eliminated: false },
  { id: 9, name: "Anna", imagePath: "/images/who-is-who/9.anna.png", features: ["auburn hair", "green eyes", "nose ring"], eliminated: false },
  { id: 10, name: "Thomas", imagePath: "/images/who-is-who/10.thomas.png", features: ["black hair", "glasses", "goatee"], eliminated: false },
  { id: 11, name: "Sofia", imagePath: "/images/who-is-who/11.sofia.png", features: ["curly hair", "brown eyes", "dimples"], eliminated: false },
  { id: 12, name: "Emily", imagePath: "/images/who-is-who/12.emily.png", features: ["straight black hair", "blue eyes", "small scar"], eliminated: false },
  { id: 13, name: "Robert", imagePath: "/images/who-is-who/13.robert.png", features: ["grey beard", "balding", "brown eyes"], eliminated: false },
  { id: 14, name: "Lucy", imagePath: "/images/who-is-who/14.lucy.png", features: ["platinum blonde hair", "pink lips", "thick eyebrows"], eliminated: false },
  { id: 15, name: "William", imagePath: "/images/who-is-who/15.william.png", features: ["red hair", "freckles", "clean shaven"], eliminated: false }
];

const WhosWhoGame = () => {
  const [people, setPeople] = useState(characters);
  const [timeLeft, setTimeLeft] = useState(60);
  const [gameOver, setGameOver] = useState(false);
  const clickSoundRef = React.useRef(null);
  const timeUpSoundRef = React.useRef(null);

  useEffect(() => {
    if (timeLeft > 0) {
      const timer = setTimeout(() => setTimeLeft(timeLeft - 1), 1000);
      return () => clearTimeout(timer);
    } else {
      setGameOver(true);
      if (timeUpSoundRef.current) {
        timeUpSoundRef.current.play();
      }
    }
  }, [timeLeft]);

  const toggleElimination = (id) => {
    if (!gameOver) {
      if (clickSoundRef.current) {
        clickSoundRef.current.play();
      }
      setPeople(people.map(person => 
        person.id === id ? { ...person, eliminated: !person.eliminated } : person
      ));
    }
  };

  const resetGame = () => {
    setPeople(characters.map(person => ({ ...person, eliminated: false })));
    setTimeLeft(60);
    setGameOver(false);
  };

  return (
    <div className="w-full max-w-6xl mx-auto p-4">
      <audio ref={clickSoundRef} src="/sounds/mouse-click.mp3" preload="auto" />
      <audio ref={timeUpSoundRef} src="/sounds/time-up.mp3" preload="auto" />

      <div className="mb-4 text-center">
        <h1 className="text-xl font-bold">Who's Who Game</h1>
        <p className={`text-lg font-semibold ${timeLeft <= 10 ? 'text-red-500' : 'text-gray-800'}`}>
          Time Left: {timeLeft}s
        </p>
        <button
          onClick={resetGame}
          className="px-4 py-2 bg-blue-500 text-white font-bold rounded shadow hover:bg-blue-600 transition mt-2"
        >
          Reset Game
        </button>
      </div>

      <div className="grid grid-cols-5 gap-4">
        {people.map((person) => (
          <Card
            key={person.id}
            className={`p-2 cursor-pointer transition-all ${
              person.eliminated ? 'opacity-50' : ''
            } ${gameOver ? 'pointer-events-none' : ''}`}
            onClick={() => toggleElimination(person.id)}
          >
            <div className="relative aspect-square">
              <img
                src={person.imagePath}
                alt={person.name}
                className="w-full h-full object-cover rounded-lg"
              />
              {person.eliminated && (
                <div className="absolute inset-0 flex items-center justify-center">
                  <X className="w-12 h-12 text-red-500" />
                </div>
              )}
            </div>
            <div className="text-center mt-2">
              <h3 className="font-bold text-sm">{person.name}</h3>
              <p className="text-xs text-gray-600">
                {person.features.join(', ')}
              </p>
            </div>
          </Card>
        ))}
      </div>

      {gameOver && (
        <div className="mt-4 text-center">
          <p className="text-xl font-bold text-red-500">Time's up! Game Over.</p>
        </div>
      )}
    </div>
  );
};

export default WhosWhoGame;